*{
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding:60px;
}
input {
  font-size: 1.5em;
  width:100%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  display: inline-block;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p{
  padding:5px;
  text-align: center;
}

.card img{
  max-height: 170px;}

  footer{
    vertical-align: bottom;
    width: 100%;
    background-color:silver;
    padding: 10px;
    color: #fff;
    font-size: 0.9em;
  }